import { CommonModule, isPlatformBrowser, NgClass } from '@angular/common';
import { Component, CUSTOM_ELEMENTS_SCHEMA, ElementRef, Inject, Input, PLATFORM_ID, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatIconModule } from '@angular/material/icon';
import { Ad } from '@core/models/ads.model';
import { AdPhotoViewerComponent } from '@shared/components/ads/ad-photo-viewer/ad-photo-viewer.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { Keyboard, Navigation, Pagination, Swiper, Zoom } from 'swiper';
import { register } from 'swiper/element';

@Component({
	selector: 'findqo-ad-image-carousel',
	standalone: true,
	imports: [MatIconModule, NgClass, CommonModule],
	templateUrl: './ad-image-carousel.component.html',
	styleUrls: ['./ad-image-carousel.component.scss'],
	schemas: [CUSTOM_ELEMENTS_SCHEMA],
})
export class AdsImageCarouselComponent {
	constructor(
		public readonly dialog: MatDialog,
		private readonly sharedAdsFacade: SharedAdsFacade,
		@Inject(PLATFORM_ID) public platformId
	) {}

	@ViewChild('swiperContainer', { static: true }) swiperContainer: ElementRef;

	@Input() isAdDetail?: boolean;
	@Input() ad?: Ad;

	mySwiper: Swiper | undefined;

	onClickImage() {
		if (this.isAdDetail) {
			this.openImageGallery();
		}
	}

	openImageGallery(): void {
		this.dialog.open(AdPhotoViewerComponent, {
			width: '100%',
			height: '100%',
			data: {
				ad: this.ad,
				sharedAdsFacade: this.sharedAdsFacade,
			},
			panelClass: 'ad-images',
		});
	}

	ngAfterViewInit(): void {
		if (isPlatformBrowser(this.platformId)) {
			register();

			this.mySwiper = new Swiper(this.swiperContainer.nativeElement, {
				modules: [Keyboard, Pagination, Navigation, Zoom],
				pagination: {
					el: '.swiper-pagination',
					type: 'fraction',
				},
				keyboard: {
					enabled: true,
				},
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev',
				},
				zoom: {
					toggle: true,
				},
				loop: true,
			});
		}
	}
}
