import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AmplitudeEvent, FeatureVersion } from '@core/enums';
import { SnackService } from '@core/services/snack.service';
import { AuthService } from '@core/data-access/services/auth.service';
import { CommonFacade } from '@core/facades/common.facade';
import { Ad, ClientUser } from '@core/models';
import { EnquiryService } from '@core/services/enquiry.service';
import { ViewportService } from '@core/services/viewport.service';
import { SendInquiryDialogComponent } from '@feature/general/send-inquiry/send-inquiry-dialog.component';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { AdsService } from '@shared/services/ads.service';
import { Observable } from 'rxjs';

interface DialogData {
	ad: Ad;
	sharedAdsFacade: SharedAdsFacade;
}

@Component({
    selector: 'findqo-ad-photo-viewer',
    templateUrl: './ad-photo-viewer.component.html',
    styleUrls: ['./ad-photo-viewer.component.scss'],
    standalone: false
})
export class AdPhotoViewerComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public readonly data: DialogData,
		private readonly dialogRef: MatDialogRef<AdPhotoViewerComponent>,
		private readonly adsService: AdsService,
		public readonly viewport: ViewportService,
		private readonly matDialog: MatDialog,
		private readonly auth: AuthService,
		private readonly commonFacade: CommonFacade,
		private readonly enquiryService: EnquiryService,
		private snackService: SnackService
	) {
		this.user$ = this.commonFacade.getUser$();
	}

	user$: Observable<ClientUser>;
	showCarousel = true;

	readonly buttonType = ButtonType;
	readonly featureVersion = FeatureVersion;

	onCall(): void {
		this.adsService.onPhoneCall(this.data.ad);
	}

	onWhatsappMessage(): void {
		this.adsService.onWhatsappMessage(this.data.ad);
	}

	back(): void {
		this.dialogRef.close();
	}

	togglePhotoView(show: boolean): void {
		this.showCarousel = show;
	}

	openSendInquiryDialog(): void {
		this.matDialog.open(SendInquiryDialogComponent, {
			width: '100%',
			height: '100%',
			position: {
				right: '0',
			},
			data: {
				ad: this.data.ad,
				sharedAdsFacade: this.data.sharedAdsFacade,
			},
			panelClass: 'inquiry-form',
			backdropClass: 'inquiry-backdrop',
		});
	}

	getMessage(status: string) {
		if (status === 'sold') {
			return 'This property is sold';
		} else if (status === 'property-occupied') {
			return 'This property is occupied';
		} else if (status === 'sale-agreed') {
			return 'This property is sale agreed';
		} else {
			return '';
		}
	}

	onSendInquiry(status: string): void {
		if (['sold', 'property-occupied', 'sale-agreed', 'expired'].includes(status) && this.viewport.desktop) {
			return null;
		}
		if (['sold', 'property-occupied', 'sale-agreed', 'expired'].includes(status) && !this.viewport.desktop) {
			this.snackService.open({
				data: { message: this.getMessage(status) },
				panelClass: 'snackbar-error',
				duration: 3000,
			});
			return;
		}
		this.enquiryService.onOpenSendEnquiryDialog(AmplitudeEvent.AD_DETAILS_SEND_ENQUIRY_CLICKED, () => this.openSendInquiryDialog());
	}
}
